import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Formik, Form } from 'formik'
import * as Yup from 'yup'
import moment from 'moment'
import styled from 'styled-components'
import { ButtonRound, ErrorText } from '../stylesheets/mainStyled'
import { device } from '../stylesheets/device'
import { ReactComponent as SearchIcon } from '../images/search_s.svg'

import { getAuth } from '../redux/global/globalSelectors'
import Loading from '../components/Loader'
import TextInput from '../components/FormikInputs/TextInput'
import MaskInput from '../components/FormikInputs/MaskInput'
import { DatePickerField } from '../components/FormikInputs/DatePicker'
import { getOrders } from '../services/apiMethods'
import { ordersAll, ordersError, ordersLoader } from '../redux/orders/ordersActions'
import { useHistory } from 'react-router'
import { getLoader } from '../redux/orders/ordersSelectors'
import { getErrror } from '../redux/reports/reportsSelectors'

const OrdersPage = () => {
  const dispatch = useDispatch()
  const history = useHistory()
  const auth = useSelector(getAuth)
  const loader = useSelector(getLoader)
  const error = useSelector(getErrror)
  const [message, setMessage] = useState('')
  console.log(loader)
  const getIsRequst = (
    orderNumber,
    ticketNumber,
    customerPhone,
    fio,
    departure,
    trip
  ) => {
    if (orderNumber || ticketNumber || customerPhone || fio || departure || trip) {
      if (fio && departure && trip) {
        return true
      }
      if (fio || departure || trip) {
        setMessage('Необходино заполнить ФИО, Дата, Рейс')
        return false
      }
      return true
    } else {
      setMessage('Необходино заполнить одно из полей')
      return false
    }
  }
  return (
    <Container>
      <Title>Введите одно или несколько полей</Title>
      <Formik
        initialValues={{
          orderNumber: '',
          ticketNumber: '',
          fio: '',
          phone: '',
          trip: '',
          date: '',
        }}
        validationSchema={Yup.object({
          orderNumber: Yup.number(),
          ticketNumber: Yup.number(),
          phone: Yup.string().min(13, 'yt'),
          //   .required('is required'),
          // memberType: Yup.string().required('is required'),
        })}
        onSubmit={async ({ orderNumber, ticketNumber, fio, phone, trip, date }) => {
          setMessage('')
          const orderId = orderNumber ? `&orderId=${orderNumber}` : ''
          const serviceId = ticketNumber ? `&serviceId=${ticketNumber}` : ''
          const customerPhone = phone ? `&customerPhone=${phone.replace(/\D+/g, '')}` : ''
          const customerName = fio ? `&customerName=${fio}` : ''
          const departure = date ? `&departure=${moment(date).format('YYYY-MM-DD')}` : ''
          // const trip =

          const isRequest = getIsRequst(
            orderId,
            serviceId,
            customerPhone,
            customerName,
            departure,
            trip
          )

          dispatch(ordersLoader())
          // if (isRequest) {
          const queryStr =
            `${orderId}${serviceId}${customerPhone}${customerName}${departure}`.slice(1)
          try {
            const { data } = await getOrders(queryStr, auth)
            if (data) {
              dispatch(ordersAll(data))
              history.push('/orders/result')
            } else {
              setMessage('По вашему запросу ничего не найдено')
            }
          } catch (error) {
            if (error.response.status === 500) setMessage(error.response.data.message)
            dispatch(ordersError(error))
          }
          // }
        }}
      >
        <Form>
          <FormBox>
            <TextInput
              label="Номер заказа"
              name="orderNumber"
              type="number"
              autoComplete="none"
            />
            <TextInput
              label="Номер билета"
              name="ticketNumber"
              type="number"
              autoComplete="none"
            />
            <MaskInput
              label="Номер телефона"
              name="phone"
              type="text"
              mask="+38(999) 999-99-99"
            />
            <TextInput label="ФИО" name="fio" type="text" autoComplete="none" />
            <TextInput
              label="Рейс"
              name="trip"
              type="text"
              autoComplete="none"
              // disabled
            />
            <DatePickerField label="Дата" name="date" />

            {error && <ErrorText>{error.message}</ErrorText>}
            {message && <ErrorText>{message}</ErrorText>}

            <ButtonRound type="submit">
              {loader ? (
                <Loading type="ThreeDots" color="#00BFFF" height={15} width={40} />
              ) : (
                <SearchIcon />
              )}
            </ButtonRound>
          </FormBox>
        </Form>
      </Formik>
    </Container>
  )
}

export default OrdersPage
const Container = styled.div`
  padding: 20px 10px 80px;

  @media ${device.mobileL} {
    width: 410px;
    margin: 0 auto;
  }
`

const Title = styled.p`
  font-size: 24px;
  text-align: center;
`
const FormBox = styled.div`
  /* padding: 20px; */
`
